import Vue from 'vue';
import Router from 'vue-router';

Vue.use(Router);

function loadView(component) {
  return () => import(`@/components/${component}.vue`);
}

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Home',
      component: loadView('Home'),
    },
    {
      path: '/home',
      name: 'Home',
      component: loadView('Home'),
    },
    {
      path: '/cheatsheet',
      name: 'CheatSheet',
      component: loadView('CheatSheet'),
    },
    {
      path: '/expense',
      name: 'Expense',
      component: loadView('Expense'),
    },
    {
      path: '/replace',
      name: 'Replace',
      component: loadView('Replace'),
    },
    {
      path: '/novel',
      name: 'Novel',
      component: loadView('Novel'),
    },
    {
      path: '/diary',
      name: 'Diary',
      component: loadView('Diary'),
    },
  ],
});
